* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-size: cover;
  background: linear-gradient(rgba(17, 17, 17, 0.8), rgba(17, 17, 17, 0.6)),
  url(https://static.vecteezy.com/system/resources/previews/001/040/178/original/abstract-gradient-background-vector.jpg) center no-repeat;
}
h1{
  padding-bottom: 20px;
}

h2,ul{
  text-align: center;
  padding: 10px;
}
.title {
  text-align: center;
  width: 30vw;
  background: rgb(34,193,195);
  background: linear-gradient(0deg, rgba(34,193,195,1) 0%, rgba(253,187,45,1) 100%);
  padding: 2vw 1vw;
  border-radius: 10px 10px 0 0;
  font-size: 1rem;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

input {
  border: 1px solid rgb(235, 232, 81);
  border-radius: 10px;
  padding: 1vw 1vw;
  outline: none;
  margin: 5px;
}

button{
  border-radius: 10px;
  font-size:larger;
  background-color: rgb(45, 100, 39);
  font-family: sans-serif;
  margin-top: 15px;
  padding: 10px;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.3s ease;}

button:hover {
    background-color: #0056b3;
  }

